:root {
    --light-bg: #f9f9f9;
    --dark-bg: #190133;
    --light-text: #f7f8fa;
    --dark-text: #100606;
    --accent: #9f6bff;
}

.dark {
    --color: var(--light-text);
    --bg: var(--dark-bg);
}

.light {
    --bg: var(--light-bg);
    --color: var(--dark-text);
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: "Encode Sans Expanded", sans-serif;
}
/* 
body {
    overflow: hidden;
} */



h1 {
    color: #9f6bff;
    color: var(--accent);
    font-size: 16px;
    font-weight: 700;
    letter-spacing: 1.4px;
    margin-bottom: 16px;
}

h2 {
    margin-bottom: 24px;
    font-size: calc(25px + 1.4vw);
    line-height: 1.1;
    font-weight: 600;
    color: var(--color);
}

p {
    font-size: 20px;
    color: var(--color);
}

.button {
    border-radius: 50px;
    background: #9f6bff;
    background: var(--accent);
    white-space: nowrap;
    padding: 14px 48px;
    color: #f7f8fa;
    color: var(--light-text);
    font-size:  16px;
    outline: none;
    border: 1px solid var(--bg);
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.5s ease-in-out;
    font-weight: 700;
    max-width: 200px;
    text-decoration: none;
}

.button:hover {
    transition: all 0.2s ease-in-out;
    background: var(--bg);
    color: #9f6bff;
    color: var(--accent);
    border-color: #9f6bff;
    border-color: var(--accent);
}

.modal-container {
    position: fixed;
    height: 100vh;
    width: 100vw;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.6);
    z-index: 100;
    
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal {
    background-color: var(--bg);
    padding: 40px;
    border-radius: 5px;
    max-width: 80%;
}

.modal h3 {
    font-size: 25px;
}

.modal p {
    margin: 40px 0px;
}

.modal .button {
    width: 100%;
    border-radius: 10px;
    max-width: initial;
}


@media (max-width: 600px) {
    .modal {
        padding: 20px;
    }

    .modal h3 {
        font-size: 20px;
    }
}
#gallery {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: var(--bg);
}

#gallery #left {
    width: 960px;
    max-width: 70%;
}

#gallery #right {
    width: calc(100vw - 1060px);
    min-width: calc(30vw - 50px);
}

#gallery #left img {
    max-width: 100%;
}

#gallery #right h2 {
    margin-bottom: 80px;
}



@media screen and (max-width: 850px) {
    #gallery {
        flex-direction: column-reverse;
    }

    #gallery #left {
        width: 100%;
        max-width: initial;
        padding: 30px 20px;
    }

    #gallery #right {
        width: initial;
        min-width: initial;
        padding: 50px 0px;
    }
}
#about {
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: var(--bg);
    padding: 100px 30px;
}

#about #left {
    max-width: min(500px, 50%);
}

#about #right {
    max-width: 550px;
}

#about img {
    max-width: 100%;
}

#about #left p {
    margin: 40px 0px 80px 0px;
}


@media screen and (max-width: 850px) {
    #about {
        flex-direction: column-reverse;
    }
    
    #about #right {
        max-width: 450px;
    }
    
    #about #left {
        max-width: initial;
        padding: 50px 20px 0px;
    }
    
    #about #left .button {
        margin: 0px auto;
    }
}
#contact {
    display: flex;
    align-items: center;
    justify-content: center;
    grid-gap: 50px;
    gap: 50px;
    padding: 100px 30px;
    background-color: var(--bg);
}

#contact #left {
    max-width: 700px;
    width: 100%;
    min-width: 400px;
}

#contact #right {
    max-width: 400px;
}

#contact img {
    max-width: 100%;
}

#contact form {
    background-color: var(--bg);
    padding: 40px;
    margin: 50px 0px;
    border-radius: 10px;
}

#contact label {
    color: var(--color);
}

#contact input, #contact textarea {
    width: 100%;
    border-radius: 5px;
    height: 50px;
    margin-bottom: 30px;
    margin-top: 10px;
    padding: 10px;
}

#contact textarea {
    height: 130px;
}

#contact button {
    width: 100%;
    border-radius: 10px;
    max-width: initial;
}

@media screen and (max-width: 850px) {
    #contact {
        flex-direction: column-reverse;
    }
    
    #contact #right {
        max-width: 300px;
    }
    
    #contact #left {
        max-width: initial;
        padding: 50px 0px 0px 0px;
    }

    #contact form {
        padding: 20px;
    }

    #contact input {
        margin-bottom: 20px;
    }
    
    #contact #left .button {
        margin: 0px auto;
    }
}
#gallery-page {
    background-color: var(--bg);
}

#gallery-page #container {
    margin: 0px auto;
    max-width: 1200px;
    padding: 80px 20px;
}

#gallery-page #grid {
    -webkit-columns: 3;
            columns: 3;
    grid-gap: 10px;
    gap: 10px;
}

#gallery-page #grid img {
    width: 100%;
    padding: 5px 0px;
}
